.home-mv {
  width: 100%;
  height: 100vh;
  background: no-repeat url(../img/home/home_mv.png) center center/cover;
  z-index: 0;
}

.home-mv__wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
}

.home-mv__wrapper::before {
  content: '';
  display: block;
  width: 181px;
  height: 264px;
  background: no-repeat url(../img/object_left.png) center center/contain;
  position: absolute;
  bottom: -204px;
  right: 30.5%;
  z-index: 1;
}

@media screen and (max-width: 56em) {
  .home-mv__wrapper::before {
    bottom: -124px;
    left: -30px;
  }
}

.home-mv__inner {
  margin: 0 auto;
  width: 100%;
  max-width: 1280px;
  padding: 0 40px;
}

@media screen and (max-width: 56em) {
  .home-mv__inner {
    padding: 0 8%;
  }
}

.home-mv .catch {
  color: #fff;
  font-size: 8.5rem;
  font-weight: bold;
  line-height: calc(120/85);
  letter-spacing: .1em;
}

@media screen and (max-width: 56em) {
  .home-mv .catch {
    font-size: 5.5rem;
    line-height: calc(70/55);
  }
}

.home-mv .scroll {
  height: 71px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  font-size: 1.2rem;
  line-height: 1;
  font-family: "Raleway", sans-serif;
  text-align: center;
}

.home-mv .scroll::after {
  content: '';
  display: block;
  margin: 1rem auto 0;
  width: 1px;
  height: 50px;
  background-color: #fff;
}

